<template>
  <div>
    <div class="mhead" v-if="!hideHead">
      <ul>
        <li>
          <text>购气写卡</text>
        </li>
      </ul>
    </div>
    <div class="loadview">
      <div class="auto">
        <img src="../../image/loading_lg.gif" width="66" height="66" border-color ="white" border-style = "solid" alt="" />
        <h3>{{prompt + loadingstrs[loadingindex]}}</h3>
      </div>
    </div>
    <div class="foot" @click="isback()">
      <text>返回</text>
    </div>
  </div>
</template>
<script>
import co from 'co'
import Vue from 'vue'
let checkValObj
let cycleValObj
let getInfoGen = function * (self) {
  self.prompt = '正在获取用户未写卡信息'
  let condition = `f_cardid = '${self.cardInfo.CardID}' AND f_aliasname like '%${self.cardInfo.Factory}%' and f_userid= '${self.Verifyuserinfo.f_user_id}' and f_write_card='未写卡' and f_payfeevalid='有效' `
  if (Vue.PosUtil.posParam.filiale.hasFiliale) {
    condition = condition + Vue.PosUtil.posParam.filiale.condition
  }
  try {
    let ret = yield  self.$resetpost(Vue.ProxyUrl + '/rs/sql/'+self.$PosUtil.posParam.revenue+'getSellUnWrite',
      {data: {condition: condition}}, {resolveMsg: null, rejectMsg: null,aoteEncrypt:Vue.PosUtil.posParam.EncryType,encryKey: Vue.PosUtil.posParam.EnceyKey})
      window.clearInterval(cycleValObj)
      console.log(ret,ret.data.length,'未写卡信息')
      if(ret.data.length<1){
          self.errorInfo('不存在未写卡信息，请线上缴费后，进行写卡')
      }else if(ret.data.length==1){
          self.stairPrices = ret.data
          console.log('即将写卡')
          self.$goto('write-card', {userInfo: self.Verifyuserinfo, cardInfo: self.cardInfo,stairPrice:self.stairPrices[0]}, 'self', self.back('tomain'))
          // self.$goto('user-info', {userInfo: self.Verifyuserinfo, cardInfo: self.cardInfo,stairPrice:self.stairPrices[0]}, 'self', self.back('tomain'))
      }else {
          self.errorInfo('未写卡信息数大于1条，请去营业厅进行处理')
      }
  } catch (error) {
     var msg = '查询未写卡信息失败,请重新查询！'
     self.errorInfo(msg)
  }
}
export default {
  title: '读卡跳转页',
  data () {
    return {
      // isloaded: false,
      cardInfo: {},
      checkindex: 0,
      prompt: '正在检查卡槽是否有卡',
      loadingindex: 0,
      loadingstrs: ['', '.', '..', '...'],
      userInfo: {},
      stairPrices:[]
    }
  },
  props: [{hideHead: false}, 'sellType','Verifyuserinfo','writeInfo'],
  methods: {
    toback(parame){
      this.$back(parame)
    },
    back () {
      window.clearInterval(checkValObj)
      window.clearInterval(cycleValObj)
      this.$back('tomain')
    },
    cycle () {
      if (this.loadingindex === this.loadingstrs.length - 1) {
        this.loadingindex = 0
      } else {
        this.loadingindex += 1
      }
    },
    readCard () {
      this.prompt = '正在读卡，请稍后'
      // 清空cardinfo缓存
      this.cardInfo = {}
      this.userInfo = {}

      let jo = this.$AndroidPlugin.read()
      console.log('jo=========>',jo)
      if (jo.code === 200) {
        this.cardInfo = jo.msg
        console.log('cardinfo=========>',this.cardInfo)
        this.prompt = '正在校验用户信息，请稍后'
        if(this.cardInfo.Gas>0||this.cardInfo.Money>0){
            this.errorInfo('卡上有余额，请上表后，再次进行写卡')
            return
        }
        if(this.Verifyuserinfo.f_card_id==this.cardInfo.CardID&&this.Verifyuserinfo.f_alias==this.cardInfo.Factory){
           console.log(this.stairPrices,'校验成功')
            this.$goto('write-card', {userInfo: this.Verifyuserinfo, cardInfo: this.cardInfo,stairPrice:this.writeInfo}, 'self', this.back('tomain'))
            // let  getWrite=getInfoGen(this)
            // co(getWrite)
        }else {
            this.errorInfo('用户信息与卡片信息不一致')
            return
        }
      } else if (jo.code === 500) {
        this.errorInfo(jo.msg + ',请检查卡是否插好！')
      } else {
        this.errorInfo('调用读卡器失败,请联系管理员！')
      }
    },
    errorInfo (msg) {
      window.clearInterval(checkValObj)
      window.clearInterval(cycleValObj)
      this.checkindex = 0
      this.$showMessage(msg + '(点击\'确定\'返回)', ['confirm', 'cancel'])
    .then((res) => {
      if (res === 'confirm') {
        this.back()
      } else {
        checkValObj = window.setInterval(this.checkCard, 1000)
      }
    })
    },
    checkCard () {
      if (this.checkindex === 0) {
        this.prompt = '正在检查卡槽是否有卡'
      }
      this.cycle()
      if (this.checkindex % 10 === 0 && this.checkindex !== 0) {
        window.clearInterval(checkValObj)
        this.errorInfo('未检测到卡,请检查卡槽是否有卡且没有异物！')
      } else if (this.checkindex % 2 === 0) {
        let ret =  Vue.AndroidPlugin.hasCard(this.checkindex)
        if (ret) {
          window.clearInterval(checkValObj)
          cycleValObj = window.setInterval(this.cycle, 1000)
          this.readCard()
        }
        this.checkindex += 1
      } else {
        this.checkindex += 1
      }
    },

    isback () {
      this.errorInfo('确定要退出吗？')
    }
    // istomain () {
    //   this.$showMessage('确定要退出吗？(点击\'确定\'返回)', ['confirm', 'cancel'])
    // .then((res) => {
    //   if (res === 'confirm') {
    //     this.tomain()
    //   } else {
    //     checkValObj = window.setInterval(this.checkCard, 1000)
    //   }
    // })
    // },
  },
  ready () {
      console.log(this.writeInfo,'asdas')
      console.log(this.Verifyuserinfo,'asdas')
      checkValObj = window.setInterval(this.checkCard, 1000)
    // var ret = HostApp.hasCard().data
    // if (ret) {
    //   HostApp.alert('1111')
    // } else {
    //   HostApp.alert('22222')
    // }
  }

}
</script>



